import React from "react";
import { Link } from "react-router-dom";
import { slug, limit } from "../_fjs/str";

const Suggest = ({ rid, type, title, image, dateposted }) => {
  const date_obj = new Date(dateposted);
  const year = date_obj.getUTCFullYear();
  const month = date_obj.getUTCMonth();
  const day = date_obj.getUTCDay();
  const fulldate = `${year}-${month}-${day}`;
  const read_link = `/${fulldate}/${Date.now()}${rid}/${slug(title)}`;
  const title_s = limit(title, 30);
  return (
    <React.Fragment>
      <div>
        <Link to={read_link}>
          <img src={image} />
          <div className="fanpage-content-latest-bottom">
            <div className="latest-pop">{type}</div>
            <h5>{title_s}</h5>
            <p>By Btsupdate</p>
          </div>
        </Link>
      </div>
    </React.Fragment>
  );
};

export default Suggest;
