import React, { useContext, useState, useEffect } from "react";
import axios from "axios";
import { ComponentLoading } from "./_context/GlobalContext";
import { DiscussionEmbed } from "disqus-react";

const Music = () => {
  const source = axios.CancelToken.source();
  const { setIfLoadedPercentage, setIfClickedLink } = useContext(
    ComponentLoading
  );
  const [albumList, setAlbumList] = useState([]);

  const fetchData = async () => {
    setIfClickedLink(true);
    try {
      const response = await axios.get(
        "https://ws.audioscrobbler.com/2.0/?method=artist.gettopalbums&artist=BTS&api_key=b6ae1d65f73146fcd56429635b8c41bb&format=json",
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
          cancelToken: source.token,
        }
      );
      const data = await response.data;
      setAlbumList(data.topalbums.album);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };
  useEffect(() => {
    fetchData();
    return () => {
      source.cancel();
    };
  }, []);
  return (
    <React.Fragment>
      <div className="artist-albums-list" style={{ display: "block" }}>
        <h5>Albums</h5>
        <div className="artist-albums-list-d">
          {albumList &&
            albumList.map((album, i) => {
              return album.name != "(null)" ? (
                <React.Fragment key={i}>
                  <a target="_blank" title="play on fm" href={album.url}>
                    <div className="artist-albums-infor">
                      <img src={album.image[3]["#text"]} alt={album.name} />
                      <p className="artist-albums-infor-p">{album.name}</p>
                      <p className="artist-albums-infor-listeners">
                        <span>{album.playcount} Listeners</span>
                      </p>
                    </div>
                  </a>
                </React.Fragment>
              ) : (
                ""
              );
            })}
        </div>
        <a
          href="https://www.last.fm/music/BTS/+albums?order=most_popular"
          target="_blank"
          className="artist-albums-infor-a"
        >
          View all albums
        </a>
        <div className="fanpage-comment">
          <div id="disqus_thread">
            <DiscussionEmbed
              shortname="bts-updates"
              config={{
                url: window.location.search,
                title: "bts-update-music",
              }}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Music;
