import React, { useState, useEffect, useContext } from "react";
import { useParams } from "react-router-dom";
import $ from "jquery";
import axios from "axios";
import parse from "html-react-parser";
import Suggest from "./Suggest";
import { ComponentLoading } from "./_context/GlobalContext";
import { DiscussionEmbed } from "disqus-react";

const Read = () => {
  const source = axios.CancelToken.source();
  const { setIfLoadedPercentage, setIfClickedLink } = useContext(
    ComponentLoading
  );
  const { id } = useParams();
  const rid = id.substring(13);
  const [postData, setPostData] = useState([]);
  const [postSuggestData, setPostSuggestData] = useState([]);
  const fetchReadPostData = async () => {
    setIfClickedLink(true);

    try {
      const response = await axios.get(
        process.env.REACT_APP_API + "post/read?rid=" + rid,
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
          cancelToken: source.token,
        }
      );
      const data = await response.data;
      setPostData(data);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };
  useEffect(() => {
    fetchReadPostData();
    return () => {
      source.cancel();
    };
  }, [rid]);

  const fetchSuggestData = async () => {
    try {
      const response = await axios.get(
        process.env.REACT_APP_API + "post/suggest?rid=" + rid
      );
      const data = await response.data;
      setPostSuggestData(data);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };
  useEffect(() => {
    fetchSuggestData();
    return () => {
      source.cancel();
    };
  }, [rid]);

  let images_des = $(".fanpage-post-desc").find("img");
  if (window.screen.availWidth <= 1800) {
    for (let i = 0; i < images_des.length; i++) {
      images_des[i].style.cssText = "width: 50% !important; height: auto;";
    }
  }
  var subdesc = $(".subdetails").children();
  for (let i = 0; i < subdesc.length; i++) {
    subdesc[i].style.color = "#CED0D1";
    subdesc[i].style.fontFamily = "OpenSansLight";
  }
  return (
    <React.Fragment>
      <div className="fanpage-post-details">
        <div className="fanpage-post-thumbnail">
          {postData.thumbnail && (
            <img
              src={postData.thumbnail}
              alt="BTS’s RM And Suga Debut In Top 10 On Billboard’s Hot 100 Songwriters Chart"
            />
          )}
        </div>
        <div className="fanpage-post-desc" style={{}}>
          <p className="fanpage-descs">
            <span /> |{" "}
            <span>
              Post type : <span>{postData.topic}</span>
            </span>
          </p>
          <h4>{postData.title}</h4>
          {postData.description}
          <div className="subdetails">
            {postData.details && parse(postData.details)}
          </div>
          <h6>
            <span style={{ color: "#00bcd4" }}>Sources : </span>
            {postData.sources}
            <span />
          </h6>
          <p>
            to read the full info of this article you can check those links
            above to redirect from their sites.
          </p>
        </div>
      </div>
      {/* suggestion */}
      <div className="fanpage-suggestion">
        <h5>You may also like</h5>
        <div className="fanpage-suggestion-list">
          {postSuggestData.map((suggest) => {
            return (
              <Suggest
                key={suggest.id}
                rid={suggest.id}
                type={suggest.topic}
                title={suggest.title}
                image={suggest.thumbnail}
                dateposted={suggest.date_posted}
              />
            );
          })}
        </div>
        <div className="fanpage-comment">
          <div id="disqus_thread">
            <DiscussionEmbed
              shortname="bts-updates"
              config={{
                url: window.location.search,
                identifier: rid,
                title: postData.title && postData.title,
              }}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Read;
