import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import Suggest from "./Suggest";
import { ComponentLoading } from "./_context/GlobalContext";
import publicIp from "public-ip";

const About = () => {
  const source = axios.CancelToken.source();
  const { setIfLoadedPercentage, setIfClickedLink } = useContext(
    ComponentLoading
  );

  const [postSuggestData, setPostSuggestData] = useState([]);
  const [isMessageSent, setIsMessageSent] = useState();
  const fetchSuggestData = async () => {
    setIfClickedLink(true);
    try {
      const response = await axios.get(
        process.env.REACT_APP_API + "post/suggest?rid=0",
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
          cancelToken: source.token,
        }
      );
      const data = await response.data;
      setPostSuggestData(data);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };
  useEffect(() => {
    fetchSuggestData();
    return () => {
      source.cancel();
    };
  }, []);

  const [nameInput, setNameInput] = useState("");
  const [emailInput, setEmailInput] = useState("");
  const [subjectInput, setSubjectInput] = useState("");
  const [messageInput, setMessageInput] = useState("");

  const submitMessage = async (e) => {
    e.preventDefault();
    setIfClickedLink(true);

    try {
      const getClientIp = await publicIp.v4({
        fallbackUrls: ["https://ifconfig.co/ip"],
      });
      const json = JSON.stringify({
        name: nameInput,
        email: emailInput,
        subject: subjectInput,
        message: messageInput,
        ipaddress: getClientIp,
      });
      const response = await axios.post(
        process.env.REACT_APP_API + "user/message",
        json,
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
        }
      );
      const data = await response.data;
      setIsMessageSent(data);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };

  return (
    <React.Fragment>
      <div className="fanpage-contact-details">
        <h4>Contact Us</h4>
        <p className="contact-desc">Fill up all forms</p>
        <div className="form-data">
          {isMessageSent &&
            (isMessageSent.success ? (
              <React.Fragment>
                <div className="alert alert-success" role="alert">
                  {isMessageSent && isMessageSent.message}
                </div>
              </React.Fragment>
            ) : (
              <React.Fragment>
                <div className="alert alert-danger" role="alert">
                  {isMessageSent && isMessageSent.message}
                </div>
              </React.Fragment>
            ))}
          <form action="" role="form" onSubmit={submitMessage}>
            <div className="form-group">
              <label htmlFor="exampleInputEmail1" className="contact-titles">
                What is your name ?
              </label>
              <input
                type="text"
                className="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                placeholder="Fullname"
                required
                name="name"
                autoComplete="off"
                value={nameInput}
                onChange={(e) => setNameInput(e.target.value)}
              />
              <small id="emailHelp" className="form-text text-muted">
                Enter a name (required)
              </small>
            </div>
            <div className="form-group">
              <label htmlFor="exampleInputEmail1" className="contact-titles">
                Email address
              </label>
              <input
                type="email"
                className="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                placeholder="Email address"
                required
                name="email"
                autoComplete="off"
                value={emailInput}
                onChange={(e) => setEmailInput(e.target.value)}
              />
              <small id="emailHelp" className="form-text text-muted">
                Enter a email address (required)
              </small>
            </div>
            <div className="form-group">
              <label htmlFor="exampleInputEmail1" className="contact-titles">
                Subject
              </label>
              <input
                type="text"
                className="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                placeholder="Subject"
                required
                name="subject"
                autoComplete="off"
                value={subjectInput}
                onChange={(e) => setSubjectInput(e.target.value)}
              />
              <small id="emailHelp" className="form-text text-muted">
                What is the subject of your concern ? (required)
              </small>
            </div>
            <div className="form-group">
              <label htmlFor="exampleInputPassword1" className="contact-titles">
                Your message
              </label>
              <textarea
                className="form-control"
                rows={5}
                required
                name="message"
                value={messageInput}
                onChange={(e) => setMessageInput(e.target.value)}
              />
              <small id="emailHelp" className="form-text text-muted">
                Enter your message (required)
              </small>
            </div>
            <button
              type="submit"
              name="contact_btn"
              className="btn btn-primary"
            >
              Send
            </button>
          </form>
        </div>
      </div>
      <div className="fanpage-suggestion">
        <h5>You may also like</h5>
        <div className="fanpage-suggestion-list">
          {postSuggestData.map((suggest) => {
            return (
              <Suggest
                key={suggest.id}
                rid={suggest.id}
                type={suggest.topic}
                title={suggest.title}
                image={suggest.thumbnail}
                dateposted={suggest.date_posted}
              />
            );
          })}
        </div>
      </div>
    </React.Fragment>
  );
};

export default About;
