import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import queryString from "query-string";
import PostCard from "./_component/PostCard";
import Pagination from "./_component/Pagination";
import { limit } from "../_fjs/str";
import { ComponentLoading } from "./_context/GlobalContext";

const Home = () => {
  const source = axios.CancelToken.source();
  const { setIfLoadedPercentage, setIfClickedLink } = useContext(
    ComponentLoading
  );
  const [postList, setPostList] = useState([]);
  const qString = queryString.parse(window.location.search);
  const pString = qString["page"];
  const url = !pString ? "post/front" : "post/front?page=" + pString;

  const fetchData = async () => {
    setIfClickedLink(true);
    try {
      const response = await axios.get(process.env.REACT_APP_API + url, {
        onDownloadProgress: (progressEvent) => {
          let percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
          setIfLoadedPercentage(percentCompleted);
        },
        cancelToken: source.token,
      });
      const data = await response.data;
      setPostList(data);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };
  useEffect(() => {
    fetchData();
    return () => {
      source.cancel();
    };
  }, []);

  return (
    <React.Fragment>
      {postList["post"] &&
        postList["post"].map((post) => {
          return (
            <PostCard
              key={post.id}
              rid={post.id}
              title={post.title}
              link={post.link}
              image={post.thumbnail}
              description={limit(post.description, 80)}
              dateposted={post.date_posted}
            />
          );
        })}
      <ul className="pagination" role="navigation">
        {postList["links"] && (
          <React.Fragment>
            {postList["links"].map((link, i) => {
              return (
                <React.Fragment key={i}>
                  <Pagination i={link} page={pString} />
                </React.Fragment>
              );
            })}
          </React.Fragment>
        )}
      </ul>
    </React.Fragment>
  );
};

export default Home;
