import React, { useState } from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  const [ifMobileNav, setIfMobileNav] = useState(false);
  const showMobileNav = () => {
    if (window.screen.availWidth < 1011) {
      if (!ifMobileNav) {
        document.querySelector(".fanpage-header-menus-mobile").style.height =
          "auto";
        setIfMobileNav(true);
      } else {
        document.querySelector(".fanpage-header-menus-mobile").style.height =
          "0px";
        setIfMobileNav(false);
      }
    }
  };

  return (
    <React.Fragment>
      <div className="fanpage-header-container">
        <div className="fanpage-header-background" />
        <div className="fanpage-header-logo">
          <Link to="/">
            <h4>
              <span>Bangtan</span>UP
            </h4>
          </Link>
        </div>
        <div className="fanpage-header-menu">
          <div className="fanpage-header-menus">
            <div>
              <a className="bars-mobile" onClick={showMobileNav}>
                <i className="fa fa-bars" />
                Browse
              </a>
              <div className="fanpage-header-menus-mobile main-menu-d">
                <div className="fanpage-header-menus-mobiles-linked">
                  <div className="fanpage-header-menus-mobiles">
                    <div>
                      <input type="text" placeholder="Search..." />
                    </div>
                    <div className="fanpage-header-menus-mobiles-links">
                      <div>
                        <Link to="/music">
                          Music list<span className="hot-pop">Hot</span>
                        </Link>
                      </div>
                      <br />
                      <div>
                        <a
                          href="https://btsdiary.com/about/schedule/new-schedule/?from=https://btsupdate.com"
                          target="_blank"
                        >
                          Schedules list
                          <span className="must-pop">Must read</span>
                        </a>
                      </div>
                      <br />
                      <div>
                        <Link to="">News update</Link>
                      </div>
                      <br />
                      <div>
                        <Link to="">Merchandise</Link>
                      </div>
                    </div>
                    <div className="fanpage-header-menus-mobiles-links">
                      <div>
                        <a href="">
                          <i className="fa fa-facebook-square" />
                          Facebook
                        </a>
                      </div>
                      <br />
                      <div>
                        <a href="">
                          <i className="fa fa-twitter" />
                          Twitter
                        </a>
                      </div>
                      <br />
                      <div>
                        <a href="">
                          <i className="fa fa-instagram" />
                          Instagram
                        </a>
                      </div>
                      <br />
                      <div>
                        <a href="">
                          <i className="fa fa-youtube" />
                          Youtube
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <a>Sections:</a>
            </div>
            <div className="fanpage-header-menus-third">
              <div>
                <Link to="/music">Music list</Link>
              </div>
              <div>
                <a
                  href="https://btsdiary.com/about/schedule/new-schedule/?from=https://btsupdate.com"
                  target="_blank"
                >
                  Schedules list
                </a>
              </div>
              <div>
                <Link to="">News update</Link>
              </div>
              <div>
                <Link to="">Merchandise</Link>
              </div>
            </div>
          </div>
          <div className="fanpage-header-menu-search">
            <div>
              <p>Bangtan details:</p>
            </div>
            <div>
              <input type="text" placeholder="search..." />
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Navbar;
