import React from "react";
import { Link } from "react-router-dom";
import { slug } from "../../_fjs/str";

const SideBarCard = ({ rid, type, title, description, dateposted }) => {
  const date_obj = new Date(dateposted);
  const year = date_obj.getUTCFullYear();
  const month = date_obj.getUTCMonth();
  const day = date_obj.getUTCDay();
  const fulldate = `${year}-${month}-${day}`;
  const read_link = `/${fulldate}/${Date.now()}${rid}/${slug(title)}`;
  return (
    <React.Fragment>
      <Link to={read_link} id="read-link-tag">
        <div>
          <span className="fanpage-content-post-sidebar-cm-list-author">
            {type}
          </span>
          <span className="fanpage-content-post-sidebar-cm-list-excerpt">
            {title}
          </span>
          <span className="fanpage-content-post-sidebar-cm-list-title">
            <span className="powerup-widget-comment-title-prefix">
              On: {dateposted}
            </span>

            {description}
          </span>
        </div>
      </Link>
    </React.Fragment>
  );
};

export default SideBarCard;
