import React, { useContext, useState, useEffect } from "react";
import axios from "axios";
import SideBarCard from "./_component/SideBarCard";
import { limit } from "../_fjs/str";
import AdsImage from "../_asset/user/img/49716465-advertisement-word-cloud-business-concept.jpg";
import { ComponentLoading } from "./_context/GlobalContext";
const Sidebar = () => {
  const source = axios.CancelToken.source();
  const [postList, setPostList] = useState([]);
  const { setIfLoadedPercentage, setIfClickedLink } = useContext(
    ComponentLoading
  );
  const fetchData = async () => {
    setIfClickedLink(true);
    try {
      const response = await axios.get(
        process.env.REACT_APP_API + "post/rand",
        {
          onDownloadProgress: (progressEvent) => {
            let percentCompleted = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            setIfLoadedPercentage(percentCompleted);
          },
          cancelToken: source.token,
        }
      );
      const data = await response.data;
      setPostList(data);
    } catch (err) {
      if (axios.isCancel(err)) {
        setIfLoadedPercentage(100);
      } else {
        throw err;
      }
    }
  };
  useEffect(() => {
    fetchData();
    return () => {
      source.cancel();
    };
  }, []);

  return (
    <React.Fragment>
      <div className="fanpage-content-post-sidebar">
        <div className="container-fluid p-0">
          <div className="fanpage-content-post-sidebar-d">
            <div className="advertisement-div">
              <img src={AdsImage} />
              <p className="ads-spn">
                Advertisement <span>|</span>
                <a href="https://www.google.com/adsense/start/">Try Adsense</a>
              </p>
              <p />
            </div>
            <div className="container-fluid p-0 fanpage-content-post-sidebar-sch mrg-top-25" />
            <div className="container-fluid p-0 fanpage-content-post-sidebar-cm mrg-top-25">
              <h5>Recent post</h5>
              <div className="fanpage-content-post-sidebar-cm-list">
                {postList.map((post) => {
                  return (
                    <SideBarCard
                      key={post.id}
                      rid={post.id}
                      type={post.type}
                      title={post.title}
                      link={post.link}
                      description={limit(post.description, 50)}
                      dateposted={post.date_posted}
                    />
                  );
                })}
              </div>
            </div>
            <div className="container-fluid p-0 fanpage-content-post-sidebar-cm mrg-top-40">
              <h5>Desclaimer</h5>
              <p className="desclaimer-txt">
                This is a fansite of bts boyband created by a fan of BTS that
                providing you an update's of bts.
                <br />
                <br />
                Contents of this site including images, videos, text etc comes
                from the rightful owners who usually share those content on
                their main websites, fansites and social medias and we grab some
                piece of data of that content (not all of content) and we will
                share here included the real source or link of that post so you
                can view/read the full content of that post on their main
                websites.
              </p>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Sidebar;
