import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
const Footer = () => {
  const [isCookieConsent, setisCookieConsent] = useState(false);
  const cookieLst = localStorage.getItem("cookie-consent");
  const acceptCookieConsent = () => {
    localStorage.setItem("cookie-consent", true);
    setisCookieConsent(true);
  };

  useEffect(() => {
    const cookieD = document.querySelector(".cookie-d");
    setisCookieConsent(cookieLst ? true : false);
    isCookieConsent
      ? (cookieD.style.display = "none")
      : (cookieD.style.display = "block");

    window.addEventListener("storage", (e) => {
      if (cookieLst) setisCookieConsent(false);
    });
  }, [isCookieConsent]);

  return (
    <React.Fragment>
      <div className="fanpage-footer fanpage-content-container">
        <div className="fanpage-footer-link-div">
          <div className="fanpage-footer-links-h">
            <ul>
              <li>
                <Link to="/about">About</Link>
              </li>
              <li>
                <Link to="/contact">Contact</Link>
              </li>
              <li>
                <Link to="/desclaimer">Desclaimer</Link>
              </li>
            </ul>
          </div>
          <div className="fanpage-footer-links-i">
            <ul>
              <li>
                <a href="/">
                  <i className="fa fa-facebook-square" />
                </a>
              </li>
              <li>
                <a href="/">
                  <i className="fa fa-twitter" />
                </a>
              </li>
              <li>
                <a href="/">
                  <i className="fa fa-instagram" />
                </a>
              </li>
              <li>
                <a href="/">
                  <i className="fa fa-youtube" />
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="cookie-d">
        <div className="cookie-consent">
          <div className="cookit-container">
            <p className="cookit-button cookit-message">
              This website uses cookies to improve your user experience, by
              clicking i accept you agree to storing cookies on your device{" "}
              <span className="cookit-link" onClick={acceptCookieConsent}>
                accept
              </span>
            </p>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Footer;
