import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { slug } from "../../_fjs/str";

const PostCard = ({ rid, title, image, description, dateposted }) => {
  const date_obj = new Date(dateposted);
  const year = date_obj.getUTCFullYear();
  const month = date_obj.getUTCMonth();
  const day = date_obj.getUTCDay();
  const fulldate = `${year}-${month}-${day}`;
  const read_link = `/${fulldate}/${Date.now()}${rid}/${slug(title)}`;

  return (
    <React.Fragment>
      <div className="fanpage-content-post-items-lists">
        <Link
          to={read_link}
          className="fanpage-content-post-items-lists-a"
          id="read-link-tag"
        >
          <img
            className="fanpage-content-post-items-lists-thumbnail"
            src={image}
            alt="btss-rm-and-suga-debut-in-top-10-on-billboards-hot-100-songwriters-chart"
          />
        </Link>
        <div className="fanpage-content-post-items-lists-title">
          <Link
            to={read_link}
            className="fanpage-content-post-items-lists-a"
            id="read-link-tag"
          >
            <h5 className="fanpage-content-post-items-lists-title-h">
              {title}
            </h5>
          </Link>
          <p className="fanpage-content-post-items-lists-title-p">
            <Link
              to={read_link}
              className="fanpage-content-post-items-lists-a"
              id="read-link-tag"
            >
              {dateposted} : <span>{description}</span>
            </Link>
            <Link
              to={read_link}
              className="fanpage-content-post-items-lists-readmore"
              id="read-link-tag"
            >
              Read more.
            </Link>
          </p>
        </div>
      </div>
    </React.Fragment>
  );
};

export default PostCard;
