import React from "react";
import { Link } from "react-router-dom";

const Pagination = ({ i, page }) => {
  const page_num = i - 1;
  return (
    <React.Fragment>
      <li className={`page-item active`}>
        <Link to={`/?page=${page_num}`} className={`page-link`}>
          {i}
        </Link>
      </li>
    </React.Fragment>
  );
};

export default Pagination;
