import React, { useEffect, useContext } from "react";
import { Switch, Route } from "react-router-dom";
import "../_asset/fonts/font.css";
import "../_asset/user/css/bootstrap.min.css";
import "../_asset/user/css/user.css";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import Home from "./Home";
import Footer from "./Footer";
import Read from "./Read";
import About from "./About";
import Desclaimer from "./Desclaimer";
import Contact from "./Contact";
import Music from "./Music";
import IndeterminateiLoader from "../Loader";
import { ComponentLoading } from "./_context/GlobalContext";
import _404 from "../404";

export const Layout = () => {
  const {
    ifLoadedPercentage,
    setIfLoadedPercentage,
    ifClickedLink,
    setIfClickedLink,
  } = useContext(ComponentLoading);

  useEffect(() => {
    setTimeout(() => {
      if (ifClickedLink) {
        if (ifLoadedPercentage === 100 || ifLoadedPercentage == Infinity) {
          setIfLoadedPercentage(0);
          setIfClickedLink(false);
        }
      }
    }, 2000);
  }, [ifClickedLink, ifLoadedPercentage]);
  return (
    <React.Fragment>
      {ifClickedLink && <IndeterminateiLoader bgcolor="#dc3545" />}
      <div className="fanpage-container-all">
        <div id="loader-lined">
          <div id="loader-line"></div>
        </div>
        <Navbar />
        <div className="fanpage-content-container">
          <div className="fanpage-content-post-list">
            <div className="container-fluid p-0">
              <div className="fanpage-content-post-item">
                <div className="fanpage-content-post-items-listed">
                  <Switch>
                    <Route path="/" exact component={Home} />
                    <Route path="/:date/:id/:title" exact component={Read} />
                    <Route path="/about" exact component={About} />
                    <Route path="/desclaimer" exact component={Desclaimer} />
                    <Route path="/contact" exact component={Contact} />
                    <Route path="/music" exact component={Music} />
                    <Route path="*" component={_404} />
                  </Switch>
                </div>
                <Sidebar />
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </React.Fragment>
  );
};

export default Layout;
