import React, { createContext, useState, useEffect } from "react";
import queryString from "query-string";
export const ComponentLoading = createContext();

export const IsLoadedContext = ({ children }) => {
  const [ifClickedLink, setIfClickedLink] = useState(false);
  const [ifLoadedPercentage, setIfLoadedPercentage] = useState(20);

  return (
    <React.Fragment>
      <ComponentLoading.Provider
        value={{
          ifLoadedPercentage,
          setIfLoadedPercentage,
          ifClickedLink,
          setIfClickedLink,
        }}
      >
        {children}
      </ComponentLoading.Provider>
    </React.Fragment>
  );
};
